import {
    Component,
    h,
    Prop,
    State,
    Element,
    Watch,
    Event,
    EventEmitter,
    Fragment,
} from '@stencil/core';
import { TimeExtraOptions, TimeUnit, TimeWithUnit } from '../../../types/time';
import { TimePresets } from '../../../models/atui-time-range.models';
import { DateRange, TimeRangeDisplay } from '../../../types/date';
import { fetchTranslations } from '../../../utils/translation';
import { TimeDateUtil } from '../../../utils/time-date.util';

/**
 * @category Form Controls
 * @description A time with unit component for selecting a time period.
 */

@Component({
    tag: 'atui-time-with-unit',
    shadow: false,
})
export class AtuiTimeWithUnitComponent {
    /**
     * Available time units for selection
     */
    @Prop()
    units: TimeUnit[];

    /**
     * Common time preset options to display
     */
    @Prop()
    common_options: TimePresets[];

    /**
     * Minimum date constraint for time selection
     */
    @Prop()
    min_date: Date = null;

    /**
     * Minimum number of seconds allowed for time selection
     */
    @Prop()
    min_seconds = 60;

    /**
     * Maximum number of seconds allowed for time selection
     */
    @Prop()
    max_seconds: number = Number.MAX_SAFE_INTEGER;

    /**
     * Initial time selection value
     */
    @Prop()
    initial_selected_time: TimeWithUnit | TimeRangeDisplay.ALL;

    /**
     * Custom error message to display when validation fails
     */
    @Prop()
    custom_error_message = null;

    /**
     * Whether to show the 'All Time' option
     */
    @Prop()
    show_all_time = false;

    @State() errorText: string = '';
    @State() secondaryErrorText: string = '';
    @State() translations: any;
    @State() dropdownOptions: (TimeUnit | TimeExtraOptions)[];
    @State() timeValue: number;
    @Watch('timeValue')
    watchTimeValue() {
        this.updateSelectedTime(this.timeValue);
    }
    @State() timeUnit: TimeUnit | TimeExtraOptions;
    @Watch('timeUnit')
    watchTimeUnit() {
        this.updateSelectedTime(this.timeValue);
    }
    @State() selectedTime: TimeWithUnit | TimeRangeDisplay.ALL;
    @State() startDate;
    @Element() el;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    componentWillRender() {
        if (!this.selectedTime) {
            this.initSelectedTime(this.initial_selected_time);
            this.initDropdownOptions();
        }
    }

    private initDropdownOptions(): void {
        this.dropdownOptions = this.show_all_time
            ? [TimeExtraOptions.ALL, ...this.units]
            : this.units;
    }

    private initSelectedTime(
        initialTimeRange: TimeWithUnit | TimeRangeDisplay.ALL,
    ): void {
        const unit =
            typeof initialTimeRange === 'object'
                ? TimeDateUtil.getCurrentOrDefaultUnit(
                      initialTimeRange.unit,
                      this.units,
                  )
                : TimeExtraOptions.ALL;
        const value =
            typeof initialTimeRange === 'object'
                ? initialTimeRange.value
                : null;

        this.timeUnit = unit;
        this.timeValue = value;
        this.selectedTime = initialTimeRange;
        this.startDate = this.getRelativeDate()?.startDate;
    }

    updateSelectedTime(value: number): void {
        const unit = this.timeUnit;

        if (unit !== TimeExtraOptions.ALL) {
            this.selectedTime = { unit, value };
        } else {
            this.selectedTime = TimeRangeDisplay.ALL;
            if (this.timeValue) {
                this.timeValue = null;
            }
        }
        this.validateInput();
        this.startDate = this.getRelativeDate()?.startDate;
    }

    validateInput() {
        if (this.selectedTime !== TimeRangeDisplay.ALL) {
            const maxSeconds = this.min_date
                ? TimeDateUtil.getSecondsAgoFromDate(this.min_date)
                : this.max_seconds;
            const maxTimeValue = TimeDateUtil.convertSecondsToUnit(
                maxSeconds,
                this.timeUnit,
                0,
                true,
            );
            const minTimeValue = TimeDateUtil.convertSecondsToUnit(
                this.min_seconds,
                this.timeUnit,
                0,
                false,
            );
            if (this.timeValue > maxTimeValue)
                this.errorText = this.translations
                    ? this.translations.ATUI.TIME.VALIDATION.MAX_NUMBER.replace(
                          '{lowerThanValue}',
                          `${maxTimeValue + 1} ${this.timeUnit}`,
                      )
                    : `Please enter a value lower than ${maxTimeValue} ${this.timeUnit}`;
            else if (this.timeValue < minTimeValue)
                this.errorText = this.translations
                    ? this.translations.ATUI.TIME.VALIDATION.MIN_NUMBER.replace(
                          '{min}',
                          `${maxTimeValue} ${this.timeUnit}`,
                      )
                    : `Please enter a value larger than ${minTimeValue} ${this.timeUnit}`;
            else this.errorText = '';
            if (this.errorText && this.custom_error_message) {
                this.secondaryErrorText = this.custom_error_message;
            }
        }
    }

    updateSelectedRange(value: TimeWithUnit): void {
        this.timeUnit = value.unit;
        this.timeValue = value.value;
        this.selectedTime = value;
    }

    getRelativeDate(): DateRange {
        if (typeof this.selectedTime === 'object') {
            return TimeDateUtil.getRelativeDateRange(this.selectedTime);
        }
    }

    clearSelection(): void {
        if (typeof this.initial_selected_time === 'object') {
            this.timeValue = this.initial_selected_time.value;
            this.timeUnit = this.initial_selected_time.unit;
        } else {
            this.timeValue = null;
            this.timeUnit = TimeExtraOptions.ALL;
        }
    }

    /**
     * Emitted when the user cancels the time selection
     */
    @Event() atuiCancel: EventEmitter<null>;
    /**
     * Emitted when the user submits the time selection
     */
    @Event() atuiSubmit: EventEmitter<TimeWithUnit | TimeRangeDisplay.ALL>;

    handleCancel(): void {
        this.atuiCancel.emit();
    }

    handleSubmit(): void {
        if (this.errorText === '') {
            this.atuiSubmit.emit(this.selectedTime);
        }
    }

    handleSelectChange(event: CustomEvent): void {
        const timeTranslationObject = this.translations.ATUI.TIME;
        const newValue = event.detail;
        this.timeUnit = (
            this.translations
                ? Object.keys(timeTranslationObject).find(
                      (key) => timeTranslationObject[key] === newValue,
                  )
                : newValue
        ) as TimeUnit;
    }

    render() {
        return (
            <div
                class="flex w-panel-sm flex-col gap-16 border border-gray-300"
                onKeyUp={(event) =>
                    (event.key === 'Enter' || event.key === ' ') &&
                    this.handleSubmit()
                }
                tabindex={0}
            >
                <div class="flex flex-col gap-8 p-12">
                    <h5 class="text-h5 font-medium text-dark">
                        {this.translations.ATUI.TIME.SELECT_RELATIVE_TIME}
                    </h5>
                    <div class="flex flex-col gap-8">
                        <atui-input-numeric
                            value={this.timeValue}
                            onAtuiChange={(event) =>
                                (this.timeValue = event.detail as number)
                            }
                        ></atui-input-numeric>
                        <atui-select
                            class="flex-fill"
                            value={this.translations.ATUI.TIME[this.timeUnit]}
                            options={
                                this.dropdownOptions
                                    ? this.dropdownOptions.map(
                                          (option) =>
                                              this.translations.ATUI.TIME[
                                                  option
                                              ],
                                      )
                                    : null
                            }
                            onAtuiChange={(event) =>
                                this.handleSelectChange(event)
                            }
                        />
                    </div>

                    {this.errorText ? (
                        <Fragment>
                            <span
                                class="text-sm text-error"
                                data-name="time-with-unit-error"
                            >
                                {this.errorText}
                            </span>
                            {this.secondaryErrorText && (
                                <span
                                    class="text-sm text-error"
                                    data-name="time-with-unit-error-secondary"
                                >
                                    {this.secondaryErrorText}
                                </span>
                            )}
                        </Fragment>
                    ) : this.selectedTime !== TimeRangeDisplay.ALL ? (
                        this.timeValue &&
                        this.selectedTime?.unit && (
                            <span class="text-sm font-normal text-med">
                                {this.startDate.toLocaleString()} ⁠— NOW
                            </span>
                        )
                    ) : (
                        this.selectedTime === TimeRangeDisplay.ALL && (
                            <span class="text-sm font-normal text-med">
                                {this.translations.ATUI.TIME.ALL_TIME_LABEL}
                            </span>
                        )
                    )}
                </div>
                {this.common_options && (
                    <div class="flex flex-col gap-8 px-12">
                        <h5 class="text-h5 font-medium text-dark">
                            {this.translations.ATUI.TIME.COMMONLY_USED}
                        </h5>
                        <div
                            class="columns-2"
                            data-name="time-with-unit-common-options"
                        >
                            {this.common_options &&
                                this.common_options.map((timerange) => (
                                    <div
                                        onClick={() =>
                                            this.updateSelectedRange(timerange)
                                        }
                                        onKeyDown={(event) => {
                                            event.stopPropagation();
                                            if (
                                                event.key === 'Enter' ||
                                                event.key === ' '
                                            )
                                                this.updateSelectedRange(
                                                    timerange,
                                                );
                                        }}
                                        tabindex={0}
                                        class={`${this.selectedTime !== TimeRangeDisplay.ALL && this.selectedTime?.value === timerange.value && this.selectedTime?.unit === timerange.unit ? 'bg-active-light px-4' : ''} cursor-pointer`}
                                    >
                                        <small>
                                            {this.translations.ATUI.TIME.LAST}{' '}
                                            {timerange.value}{' '}
                                            {
                                                this.translations.ATUI.TIME[
                                                    timerange.unit
                                                ]
                                            }
                                        </small>
                                    </div>
                                ))}
                        </div>
                    </div>
                )}
                <footer class="flex justify-between p-8">
                    <atui-button
                        type="secondaryOutline"
                        data-name="clear"
                        label={this.translations.ATUI.RESET}
                        onAtuiClick={() => this.clearSelection()}
                    ></atui-button>

                    <div class="flex gap-8">
                        <atui-button
                            type="secondaryOutline"
                            data-name="cancel"
                            label={this.translations.ATUI.CANCEL}
                            onAtuiClick={() => this.handleCancel()}
                        ></atui-button>
                        <atui-button
                            data-name="apply"
                            label={this.translations.ATUI.APPLY}
                            onAtuiClick={() => this.handleSubmit()}
                        ></atui-button>
                    </div>
                </footer>
            </div>
        );
    }
}
